import { Component, Prop, Vue } from "vue-property-decorator";

import { firestore, storage, auth } from '@/firebaseInit';
import { AuditFields } from '@/dto/AuditFields';
import { FirestoreService } from '@/services/FirestoreService';
import { UtilityService } from '@/services/UtilityService';
import { SubCategoryInfo } from "@/dto/SubCategoryInfo";

@Component
export default class SubCategoryV2 extends Vue {
  @Prop({ required: false, default: function () { return new SubCategoryInfo() } })
  subCategoryInfo!: SubCategoryInfo;
  @Prop({ required: false, default: false })
  edit!: boolean;

  @Prop({ required: true })
  parentId!: string;
  selectedImageUrl = "";
  selectedImage: any = null;
  loading = false;
  label = "Add Sub Category";
  uploadRules = [
    (value: any) => !!value || 'logo is required',

  ]
  async mounted() {
    this.label = this.edit ? "Edit Sub Category" : "Add Sub Category";
    if (this.edit) {
      this.selectedImageUrl = this.subCategoryInfo.logoUrl;
      this.uploadRules = [

      ]
    } else {
      this.resetForm();
      this.subCategoryInfo = new SubCategoryInfo();
    }
  }
  async validateForm() {
    if ((this.$refs.sellerForm as Vue & { validate: () => boolean }).validate()) {
      await this.processRequest();
    }
  }
  resetForm() {
    this.subCategoryInfo = new SubCategoryInfo();
    this.selectedImage = null;
    (this.$refs.sellerForm as Vue & { reset: () => boolean }).reset();
  }
  previewImage() {
    console.log(this.selectedImage)
    if (this.selectedImage)
      this.selectedImageUrl = URL.createObjectURL(this.selectedImage)
    else
      this.selectedImageUrl = ''
  }
  clearImages() {
    this.selectedImageUrl = ""
  }
  async processRequest() {
    try {
      if (this.edit)
        await this.updateSeller();
      else {
        await this.addSeller();

      }
    } catch (error) {

    }
  }
  async addSeller() {
    if (this.selectedImage != null) {
      try {
        this.loading = true;
        this.subCategoryInfo.id = UtilityService.convertToKebabCase(this.subCategoryInfo.name) as string;
        this.subCategoryInfo.searchPhrase = this.subCategoryInfo.name.toLowerCase();
        const fileExtension = this.getFileExtension(this.selectedImage);
        const metadata = {
          cacheControl: 'public,max-age=172800',
        };
        const resp = await storage.ref('sub-category-logos/' + this.subCategoryInfo.id + '-logo.' + fileExtension).put(this.selectedImage, metadata);
        const url = await resp.ref.getDownloadURL();
        this.subCategoryInfo.logoUrl = url;
        const dateNow = new Date();
        const auditfield: AuditFields = {
          createdBy: { uid: auth.currentUser?.uid, name: auth.currentUser?.displayName, email: auth.currentUser?.email },
          createdOn: dateNow.getTime()
        }
        this.subCategoryInfo.parentId = this.parentId;

        console.log(url);
        this.subCategoryInfo.auditTrail = auditfield;
        const dbResp = await FirestoreService.addDocumentWithId(`parentCategories/${this.parentId}/subCategories`, this.subCategoryInfo.id, this.subCategoryInfo, SubCategoryInfo.converter);
        Vue.toasted.show('Sub Category Added Successfully', { icon: 'face', type: 'success' });

        this.resetForm();
      } catch (error) {
        console.log(error)
        Vue.toasted.show(error.message, { icon: 'cross', type: 'error' });

      } finally {
        this.$store.commit('setCloseSeller', false)
        this.loading = false;
      }
    }
  }
  getFileExtension(selectedImage: any) {
    return selectedImage.name.substring(selectedImage.name.lastIndexOf(".") + 1);
  }

  cancelEdit() {
    this.$store.commit('setCloseSeller', false)
  }
  async updateSeller() {

    try {
      this.loading = true;
      this.subCategoryInfo.id = UtilityService.convertToKebabCase(this.subCategoryInfo.name) as string;
      this.subCategoryInfo.searchPhrase = this.subCategoryInfo.name.toLowerCase();

      if (this.selectedImage != null) {
        const fileExtension = this.getFileExtension(this.selectedImage);
        const metadata = {
          cacheControl: 'public,max-age=172800',
        };
        const resp = await storage.ref('sub-category-logos/' + this.subCategoryInfo.id + '-logo.' + fileExtension).put(this.selectedImage, metadata);
        const url = await resp.ref.getDownloadURL();
        this.subCategoryInfo.logoUrl = url;
        console.log(this.subCategoryInfo.logoUrl);

      }

      this.subCategoryInfo.id = UtilityService.convertToKebabCase(this.subCategoryInfo.name) as string;
      this.subCategoryInfo.parentId = this.parentId;

      const dateNow = new Date();
      const auditfield: AuditFields = {
        createdOn: this.subCategoryInfo.auditTrail.createdOn,
        createdBy: this.subCategoryInfo.auditTrail.createdBy,
        updatedBy: { uid: auth.currentUser?.uid, name: auth.currentUser?.displayName, email: auth.currentUser?.email },
        updatedOn: dateNow.getTime()
      }
      this.subCategoryInfo.auditTrail = auditfield;
      const dbResp = await FirestoreService.setDocument(`parentCategories/${this.parentId}/subCategories`, this.subCategoryInfo.id, this.subCategoryInfo, SubCategoryInfo.converter);
      Vue.toasted.show('Sub Category Updated Successfully', { icon: 'face', type: 'success' });
      this.resetForm();
    } catch (error) {
      console.log(error)
      Vue.toasted.show(error.message, { icon: 'cross', type: 'error' });
    } finally {
      this.loading = false;
      this.$store.commit('setCloseSeller', false)
    }
  }




}
