import { classToPlain, plainToClass } from 'class-transformer';
import { AuditFields } from './AuditFields';

export class SubCategoryInfo {
    id: string;
    parentId: string;
    name: string;
    description: string;
    logoUrl: string;
    auditTrail!: AuditFields;
    searchPhrase: string;

    constructor() {
        this.id = "";
        this.name = "";
        this.description = "";
        this.logoUrl = "";
        this.searchPhrase = "";
        this.parentId = "";
    }
    static converter = {
        toFirestore(expense: SubCategoryInfo): firebase.firestore.DocumentData {
            return classToPlain(expense);
        },
        fromFirestore(
            snapshot: firebase.firestore.QueryDocumentSnapshot,
            options: firebase.firestore.SnapshotOptions
        ): SubCategoryInfo {
            const data = snapshot.data(options)!;
            return plainToClass(SubCategoryInfo, data);
        }
    };
}