import { Component, Vue, Watch } from "vue-property-decorator";


import { FirestoreService } from '@/services/FirestoreService';
import Swal from 'sweetalert2';

import SubCategoryV2 from '@/components/SubCategoryV2/SubCategoryV2.vue'
import { ParentCategoryInfo } from '@/dto/ParentCategoryInfo';
import { SubCategoryInfo } from "@/dto/SubCategoryInfo";
@Component({
  components: {
    SubCategoryV2
  }
})
export default class SubCategories extends Vue {
  searchItems: ParentCategoryInfo[] = [];
  sellerSearchLoading = false;
  subCategoryList: SubCategoryInfo[] = [];
  loading = false;
  searchDisabled = false;
  queryString = "";
  searchLoading = false;
  selectedParentId = "";
  isParentAvailable = false;
  selectedMainCategory: any = undefined;
  edit = false;
  headers: any = [
    { text: 'Date Added', value: 'auditTrail.createdOn', },
    { text: 'Sub Category Id', value: 'id' },
    { text: 'Sub Category Name', value: 'name' },
    { text: 'Logo', value: 'logoUrl' },


    { text: 'Added By', value: 'auditTrail.createdBy.email' },
    { text: 'Updated By', value: 'auditTrail.updatedBy.email' },
    { text: 'Actions', value: 'actions', sortable: false },

  ]
  editedIndex = 0;
  editedItem: ParentCategoryInfo = new ParentCategoryInfo();
  dialog = false;
  isParentLoading = false;
  search = "";

  @Watch("search")
  async onPropertyChanged(value: string, oldValue: string) {
    try {
      if (value && value !== "" && value.length >= 3) {
        this.isParentLoading = true;
        const snapshot = await FirestoreService.getdocsStartingWithKey(
          `parentCategories`,
          value.toLowerCase(),
          "searchPhrase",
          ParentCategoryInfo.converter
        );
        if (!snapshot.empty) {
          snapshot.forEach((doc: any) => {
            this.searchItems.push(doc.data());
          });
        } else {
          this.searchItems = [];
        }
      }
    } catch (error) {
      Vue.toasted.show(error.message, { icon: "face", type: "error" });
    } finally {
      this.isParentLoading = false;
    }
  }
  onProductChange(item: any) {
    if (item !== null) {
      this.selectedMainCategory = item as ParentCategoryInfo;
      this.isParentAvailable = true;

    } else {
      this.selectedMainCategory = undefined;
      this.isParentAvailable = false;
      this.subCategoryList = [];

    }
  }
  addSubCategory() {
    this.edit = false;
    this.$store.commit('setCloseSeller', true);
  }
  async searchProducts() {
    console.log('query:' + this.queryString)
    if (this.queryString !== "") {
      await this.searchQueryString();
    } else {
      await this.getProducts();
    }
  }
  async getProducts() {
    try {
      this.subCategoryList = [];
      this.sellerSearchLoading = true;
      const snapshot = await FirestoreService.getDocuments(`parentCategories/${this.selectedParentId}/subCategories`, ParentCategoryInfo.converter);
      if (!snapshot.empty) {
        snapshot.forEach((doc: any) => {
          this.subCategoryList.push(doc.data());
        });
        Vue.toasted.show('Search complete', { icon: 'face', type: 'success' });

      } else {
        Vue.toasted.show('No results found', { icon: 'face', type: 'error' });
      }


    } catch (error) {
      console.log(error)

    }
    finally {
      this.sellerSearchLoading = false;
    }

  }
  editItem(item: SubCategoryInfo) {
    this.edit = true;
    this.editedIndex = this.subCategoryList.indexOf(item)
    this.editedItem = Object.assign({}, item)
    this.$store.commit('setCloseSeller', true)
  }



  async searchQueryString() {
    try {
      this.searchDisabled = true;
      this.searchLoading = true;
      this.subCategoryList = [];
      const snapshot = await FirestoreService.getdocsStartingWithKey(`parentCategories/${this.selectedParentId}/subCategories`, this.queryString, 'name', ParentCategoryInfo.converter);
      if (!snapshot.empty) {
        snapshot.forEach((doc: any) => {
          this.subCategoryList.push(doc.data());
        });
        Vue.toasted.show('Found ' + this.subCategoryList.length + ' items', { icon: 'face', type: 'success' });

      } else {
        Vue.toasted.show('No results found', { icon: 'face', type: 'error' });
      }

    } catch (error) {
      Vue.toasted.show(error.message, { icon: 'face', type: 'error' });

    } finally {
      this.searchLoading = false;
      this.searchDisabled = false;
      this.queryString = ""
    };
  }
  async deleteItem(item: SubCategoryInfo) {
    try {
      const res = await Swal.fire({
        title: 'Are you sure?',
        text: "You won't be able to revert this!",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Yes, delete it!',

      });
      if (res.isConfirmed) {
        await FirestoreService.deleteDocument(`parentCategories/${this.selectedParentId}/subCategories`, item.id);
        const deleteIndex = this.subCategoryList.indexOf(item)

        this.subCategoryList.splice(deleteIndex, 1)
        Swal.fire(
          'Deleted!',
          'Sub Category has been deleted.',
          'success'
        )
      }
    } catch (error) {
      Vue.toasted.show(error.message, { icon: 'face', type: 'error' });

    }

  }
}
